import { Component, OnInit } from '@angular/core';
declare var window: any;

@Component({
  selector: 'app-quicklink',
  templateUrl: './quicklink.page.html',
  styleUrls: ['./quicklink.page.scss'],
})
export class QuicklinkPage implements OnInit {
  constructor() { }
  working:Boolean = false
  ngOnInit() {
  }
  onClick(ql : any) : void{
    this.working = true;
    window.hybrid_layer.quick_link(ql)
    this.working = false;

  }
}
